import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ResourceFormContent } from 'src/app/shared/dashboard-resource/resource-form/resource-form.component';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal, NgbModalRef, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-permission-form',
  templateUrl: './permission-form.component.html',
  styleUrls: ['./permission-form.component.scss']
})
export class PermissionFormComponent extends ResourceFormContent {
  title: string;
  message: string;
  formGroup: FormGroup;

  @ViewChild('content') content: ElementRef<HTMLElement>;

  constructor(http: HttpClient, route: ActivatedRoute, router: Router, service: NgbModal) {
    super(http, route, router, service);

    this.formGroup = new FormGroup({
      name: new FormControl(null, Validators.required),
      description: new FormControl(null, Validators.required),
    });
  }

  openModal(): NgbModalRef {
    const options: NgbModalOptions = {
      backdrop: 'static',
      keyboard: false,
    };
    return this.service.open(this.content, options);
  }

  postUrl(): string {
    return 'permissionsCreateUrl';
  }

  putUrl(): string {
    return 'permissionsUpdateUrl';
  }
}
