import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { IndexComponent } from './index/index.component';
import { PermissionsResolver } from './permissions.resolver';
import { PermissionFormComponent } from './permission-form/permission-form.component';
import { PermissionDetailsComponent } from './permission-details/permission-details.component';
import { PermissionDeleteComponent } from './permission-delete/permission-delete.component';

const routes: Routes = [
  {
    path: '',
    component: IndexComponent,
    resolve: {
      permissions: PermissionsResolver
    },
    children: [
      {
        path: 'create',
        component: PermissionFormComponent,
      },
      {
        path: ':id/edit',
        component: PermissionFormComponent,
      },
      {
        path: ':id/details',
        pathMatch: 'full',
        component: PermissionDetailsComponent,
      },
      {
        path: ':id/delete',
        pathMatch: 'full',
        component: PermissionDeleteComponent,
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PermissionsRoutingModule { }
